import React from 'react';

import style from './CardContacts.module.scss';
import '../../../scss/index.scss';

const arr = Array.from({ length: 10 });

export const CardContacts = () => {
  return (
    <div className={style.box}>
      <div className={style.header}>
        <div className={style.checkbox}>
          <input type="checkbox" />
        </div>
        <div className={style.name}></div>
        <div className={style.data}></div>
        <div className={style.quantity}></div>
      </div>
      {/* ,     */}
      <div className="container">
        <div className={style.cards}>
          {arr.map((e, i) => {
            return (
              <div className="cardMix">
                <div className={style.carditem}>
                  <div className={style.checkbox}>
                    {/*      */}
                    <input type="checkbox" />
                  </div>
                  <div className={style.name}>unicorn@gmail.com</div>
                  <div className={style.data}>15.05.2023</div>
                  <div className={style.quantity}> </div>
                  <button className={style.buttonEdit}></button>
                </div>
              </div>
            );
          })}
        </div>
      </div>
    </div>
  );
};
